import React from 'react'
import QRCode from 'qrcode.react'
import {useSelector} from "react-redux";
import AppId from "../../../configs/appId";
import {Input} from 'reactstrap'
import {FormattedMessage} from "react-intl";
import {Clipboard} from "react-feather";
import {toastSuccess} from "../../../utility";
import {IMAGE} from "../../../configs/app-configs";

export const ShareWithFriends = () => {

  const {user} = useSelector(state => state.customizer.appId === AppId.ELITE_APP ? state.auth.guest : state.auth)

  const shareUrl = `${document.location.origin}/home?refId=${user.username}`

  const onClickCopy = async () => {
    await navigator.clipboard.writeText(shareUrl);
    toastSuccess(<FormattedMessage id='setting.copySuccess' />)
  }

  const imageSettings = {
    src : IMAGE.INON_LOGO,
    width : 100,
    excavate : true
  }

  return (
    <React.Fragment>
      <div className='text-center mb-4'>
        <QRCode id='qrcode'
                value={shareUrl}
                size={300}
                level={'H'}
                imageSettings={imageSettings}
                includeMargin={true}/>

        <div className='d-flex align-items-center justify-content-center mt-2'>
          <Input value={shareUrl} disabled/>
          <span className='cursor-pointer ml-2' onClick={onClickCopy}><Clipboard/></span>
        </div>
      </div>
    </React.Fragment>
  )
}
